var headerFilePathArray = new Array();
function removeHeadeFileRefrance(path){
	headerFilePathArray.remove(path);
}

(function(window) {
    var htmlDOC = window.document;	
    window.loadFile = function(includeFileArray, prefix, parent, callBackFunction) {
        var loadedcnt = 0;
        var error = false;
        var fileObj;
        function loadfile() {
            var filePath = includeFileArray[loadedcnt];
            if (isLoaded(filePath)) {
                loadNext();
            } else {
                var type = (filePath.substr(filePath.lastIndexOf('.') + 1)).toLowerCase();
                if (type === 'js') {
                    fileObj = htmlDOC.createElement("script");
                    fileObj.type = "text/javascript";
                } else if (type === 'css') {
                    fileObj = htmlDOC.createElement("link");
                    fileObj.type = "text/css";
                    fileObj.rel = "stylesheet";
                }
                if (prefix !== "" && prefix !== null) {
                    fileObj.id = prefix + '_' + (loadedcnt);
                }	
                if(isBelowIE10){
					fileObj.onreadystatechange = function () {
						if (fileObj.readyState === 'loaded' || fileObj.readyState === 'complete') {
							fileObj.onreadystatechange = null;
							loadNext();
						}
					};
				}else{
                    fileObj.onload = function(_, isAbort) {
						if ((type === 'css' && navigator.userAgent.toLowerCase().indexOf('applewebkit'))){
							return false;
						}
						loadNext();
					};
				}
                fileObj.onerror = function() {
                    if (!error) {
                        error = true;
                        alert("File Not Found " + filePath);
                        return;
                    }
                };

                if (type === 'js') {
                    fileObj.src = filePath;
                } else if (type === 'css') {
                    fileObj.href = filePath;
                }
				if (type === 'css' && navigator.userAgent.toLowerCase().indexOf('applewebkit')){
                   var cssnum = document.styleSheets.length;
				   var ti = setInterval(function() {
					if (document.styleSheets.length > cssnum) {
					  clearInterval(ti);
					  loadNext();
					}
				  }, 50);
                }
                var head = window.document.head || window.document.getElementsByTagName("head")[0];
                if (parent === 'head' || parent === "" || parent === null) {
                    headerFilePathArray.push(filePath);
                    head.appendChild(fileObj);
                } else {
                    var parentObj = window.document.getElementById(parent);
                    parentObj.appendChild(fileObj);
                }

            }
        }
        var loadNext = function() {
            fileObj.onload = fileObj.onreadystatechange = null;
            loadedcnt++;
            if (loadedcnt < includeFileArray.length) {
                loadfile();
            } else if (loadedcnt === includeFileArray.length) {
                if (typeof callBackFunction === "function") {
                    callBackFunction();
                }
            }
        };
        var isLoaded = function(fpath) {
            var flag = false;
            for (var i = 0; i < headerFilePathArray.length; i++) {
                if (headerFilePathArray[i] === fpath) {
                    flag = true;
                    break;
                }
            }
            return flag;
        };
        loadfile();
    };

})(window);
